<template>
  <div v-if="modelValue" class="disclaimer-popup">
    <div class="disclaimer-content">
      <h3>Disclaimer</h3>
      <p>We recommend using a secondary wallet with no assets before connecting to prepare for potential external threats. Additionally, make sure to disconnect your wallet after finishing the game. We cannot guarantee the safety of your assets against external threats.</p>
      
      <div class="disclaimer-checkbox">
        <label>
          <input type="checkbox" v-model="agreed" />
          I Agree
        </label>
      </div>
      
      <div class="disclaimer-buttons">
        <button @click="handleAgree" :disabled="!agreed" class="agree-button">Continue</button>
        <button @click="$emit('update:modelValue', false)" class="cancel-button">Cancel</button>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref } from 'vue'
import store from '@/vuex'

const props = defineProps({
  modelValue: Boolean
})

const emit = defineEmits(['update:modelValue'])
const agreed = ref(false)

const handleAgree = () => {
  if (agreed.value) {
    store.commit('setDisclaimerAgreed', true)
    emit('update:modelValue', false)
  }
}
</script>

