<template>
  <div class="full-game-history">
    <h3>Your Last Games</h3>
    <p class="description">This section shows your last 50 games history.</p>
    <div class="full-game-history__wrap">
      <table v-if="displayedGames.length">
        <thead>
          <tr>
            <th>Game Id</th>
            <th>Score</th>
            <th>Date</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(game, index) in displayedGames" :key="index">
            <td><router-link :to="`/games/${game.gameId}`">{{ game.gameId }}</router-link></td>
            <td>{{ game.score }}₿</td>
            <td>{{ formatDate(game.timestamp) }}</td>
          </tr>
        </tbody>
      </table>
      <p v-else>No game history available</p>
      <button v-if="!showAll && gameHistory.length > 10" @click="toggleShowAll" class="btn btn-primary">Show More</button>
      <button v-if="showAll" @click="toggleShowAll" class="btn btn-secondary">Show Less</button>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';

export default {
  name: 'FullGameHistory',
  computed: {
    ...mapGetters(['gameHistory']),
    displayedGames() {
      return this.showAll ? this.gameHistory : this.gameHistory.slice(0, 10);
    }
  },
  data() {
    return {
      lastAddedIndex: null,
      showAll: false,
    };
  },
  methods: {
    ...mapActions(['fetchUserGameHistory']),
    formatDate(timestamp) {
      return new Date(timestamp).toLocaleString('en-US', {
        year: 'numeric',
        month: 'short',
        day: 'numeric',
        hour: '2-digit',
        minute: '2-digit'
      });
    },
    toggleShowAll() {
      this.showAll = !this.showAll;
    }
  },
  created() {
    const userTag = this.$store.state.session.userTag;
    this.fetchUserGameHistory(userTag);
  }
}
</script>

