<template>
  <div class="page-mybitcat">
    <div class="login-section">
      <h2>My BITCAT Profile</h2>
      <p class="description-wallet"><strong>You don't need to connect your wallet to play</strong>. Just create an account and start tracking your progress. <button class="more-info-button" @click="showAccountInfo = true">More Info</button></p>
      <div class="page-mybitcat__wallet" @click="handleWalletClick">
        <wallet-multi-button></wallet-multi-button>
      </div>
      <div v-if="walletAddress && store.getters.walletAddress == walletAddress && store.getters.isAuthenticated" class="linked-wallet">
        <span class="linked-wallet__title">Linked wallet</span>
        <span class="linked-wallet__address">{{ walletAddress }}</span>
        <button class="linked-wallet__btn-unlink" @click="unlinkWallet" :disabled="isUnlinking">{{ isUnlinking ? 'Unlinking...' : 'Unlink Wallet' }}</button>
      </div>
      <div v-if="walletAddress && store.getters.walletAddress != walletAddress && store.getters.isAuthenticated" class="linked-wallet">
        <span class="linked-wallet__title">Linked wallet</span>
        <span class="linked-wallet__address">None</span>
        <button class="linked-wallet__btn-unlink" @click="linkWallet" :disabled="isLinking">{{ isLinking ? 'Linking...' : 'Link Wallet' }}</button>
        <span v-if="walletError" class="wallet-error">{{ walletError }}</span>
      </div>
      <p class="description">
        {{ session?.isAuthenticated ? 'WELCOME' : 'LOGIN' }}
      </p>
      <div class="input-group">
        <input 
          type="text" 
          v-model="userTag" 
          placeholder="Enter your user tag (a-z, 0-9, _-)"
          :class="{ 
            'has-value': userTag, 
            'error': tagError,
            'disabled': session?.isAuthenticated 
          }"
          @input="validateUserTag"
          maxlength="12"
          :disabled="session?.isAuthenticated"
        >
        <button 
          v-if="!session?.isAuthenticated && !usePassword"
          @click="handleUserTag" 
          class="save-button" 
          :disabled="!isValidTag || isCheckingTag || (usePassword && !isValidPassword)"
        >
          {{ isCheckingTag ? 'Checking...' : 'Login' }}
        </button>
        <button 
            v-else-if="!session?.isAuthenticated && usePassword"
            @click="handlePasswordSubmit"
            class="password-submit-button"
            :disabled="!isValidPassword"
          >
            Login
          </button>
        <button 
          v-else
          @click="handleLogout" 
          class="logout-button"
        >
          Logout
        </button>
      </div>
      <div v-if="tagError" class="error-message">{{ tagError }}</div>

      <div class="password-option">
        <span 
          class="password-toggle"
          :class="{ 'active': usePassword }"
          @click="handlePasswordClick"
        >
          {{ session?.isAuthenticated ? 'Change password' : 'Use password' }}
        </span>
      </div>
      
      <div v-if="usePassword" class="password-input-section">
        <div v-if="!session?.isAuthenticated" class="password-input-group">
          <input 
            type="password" 
            v-model="password"
            placeholder="Enter password (min 6 characters)"
            :class="{ 'error': passwordError }"
            @input="validatePassword"
            @keyup.enter="handlePasswordSubmit"
          >
        </div>
        <div v-else class="password-input-group">
          <input 
            v-if="hasPassword"
            type="password" 
            v-model="currentPassword"
            placeholder="Current password"
            :class="{ 'error': currentPasswordError }"
          >
          <input 
            type="password" 
            v-model="newPassword"
            placeholder="New password (min 6 characters)"
            :class="{ 'error': newPasswordError }"
            @input="validateNewPassword"
          >
          <button 
            @click="handleChangePassword" 
            class="change-password-button"
            :disabled="(!isValidNewPassword) || (hasPassword && !currentPassword)"
          >
            Change
          </button>
        </div>
        <div v-if="passwordError" class="error-message">{{ passwordError }}</div>
        <div v-if="currentPasswordError" class="error-message">{{ currentPasswordError }}</div>
        <div v-if="newPasswordError" class="error-message">{{ newPasswordError }}</div>
      </div>

      <p class="description" :class="{ 'password-required': existingTagRequiresPassword }">
        {{ getAuthDescription }}
      </p>
      <p v-if="!session?.isAuthenticated" class="description description_orange">
        Have fun with BITCAT!
      </p>
    </div>

    <div class="character-section">
      <h3>Character Stats</h3>
      <div class="character-container">
        <div class="character-profile">
          <img :src="session?.settings?.avatar || bitcatImage" alt="Your BITCAT" class="cat-avatar">
          <div class="character-info">
            <h2 class="user-tag">{{ session?.userTag || userTag || 'No Named Cat' }}</h2>
            <div class="basic-stats">
              <div class="stat">
                <label>Best score:</label>
                <span>{{ bestScore }}₿ <small></small></span>
              </div>
              <div class="stat">
                <label>Weight:</label>
                <span>{{ totalScore }}₿ <small></small></span>
              </div>
              <div class="stat">
                <label>Earned:</label>
                <span>0 <small>BITCAT</small></span>
              </div>
            </div>
            <!-- <div class="multiplayer-stats">
              <h4>Multiplayer</h4>
              <div class="stat">
                <label>Wins:</label>
                <span>{{ multiplayerStats.wins }}</span>
              </div>
              <div class="stat">
                <label>Losses:</label>
                <span>{{ multiplayerStats.loses }}</span>
              </div>
            </div> -->
            <div class="multiplayer-stats">
              <h4>Hide & Seek</h4>
              <div class="stat">
                <label>Wins:</label>
                <span>{{ store.state.hideSeek.stats?.wins || 0 }}</span>
              </div>
              <div class="stat">
                <label>Losses:</label>
                <span>{{ store.state.hideSeek.stats?.losses || 0 }}</span>
              </div>
            </div>
          </div>
        </div>
        
        <div class="stats-container">
          <div class="stat-item">
            <label>Strength</label>
            <div class="stat-bar">
              <div class="stat-value" :style="{ width: (stats.strength * 10) + '%' }"></div>
            </div>
            <button @click="upgradeStat('strength')" :disabled="!canUpgrade">+</button>
          </div>
          
          <div class="stat-item">
            <label>Agility</label>
            <div class="stat-bar">
              <div class="stat-value" :style="{ width: (stats.agility * 10) + '%' }"></div>
            </div>
            <button @click="upgradeStat('agility')" :disabled="!canUpgrade">+</button>
          </div>
          
          <div class="stat-item">
            <label>Stamina</label>
            <div class="stat-bar">
              <div class="stat-value" :style="{ width: (stats.stamina * 10) + '%' }"></div>
            </div>
            <button @click="upgradeStat('stamina')" :disabled="!canUpgrade">+</button>
          </div>
          
          <div class="stat-item">
            <label>Luck</label>
            <div class="stat-bar">
              <div class="stat-value" :style="{ width: (stats.luck * 10) + '%' }"></div>
            </div>
            <button @click="upgradeStat('luck')" :disabled="!canUpgrade">+</button>
          </div>
          
          <div class="stat-item">
            <label>Intelligence</label>
            <div class="stat-bar">
              <div class="stat-value" :style="{ width: (stats.intelligence * 10) + '%' }"></div>
            </div>
            <button @click="upgradeStat('intelligence')" :disabled="!canUpgrade">+</button>
          </div>
        </div>
      </div>
    </div>
    <div class="game-history-section">
      <FullGameHistory />
    </div>
    <DisclaimerPopup v-model="showDisclaimer" />
    <AccountInfoPopup v-model="showAccountInfo" />
  </div>
</template>

<script setup>
import { WalletMultiButton } from 'solana-wallets-vue'
import DisclaimerPopup from '@/components/DisclaimerPopup.vue'
import FullGameHistory from '@/components/FullGameHistory.vue'
import AccountInfoPopup from '@/components/AccountInfoPopup.vue'
import { useWallet } from 'solana-wallets-vue'
import { ref, computed, watch, onMounted, onUnmounted } from 'vue'
import store from '@/vuex'
import bitcatImage from '@/assets/bitcat.webp'

const { publicKey, connected } = useWallet()

onMounted(() => {
  if(!session.value?.isAuthenticated) {
    usePassword.value = true
  }
  fetchHideSeekStats()

  // window.addEventListener('storage', (e) => {
  //   if (e.key === 'walletAddress') {
  //     console.log('Wallet address changed in another tab:', e.newValue);
  //     // Обновляем данные при изменении кошелька
  //     if (e.newValue) {
  //       fetchUserBalance();
  //     }
  //   }
  // });
})

// onUnmounted(() => {
//   window.removeEventListener('storage', handleStorageChange);
// })

// Следим за подключением кошелька
watch(connected, (isConnected) => {
  if (isConnected && publicKey.value) {
    localStorage.setItem('walletAddress', publicKey.value.toString())
  } else {
    localStorage.removeItem('walletAddress')
  }

  walletAddress.value = localStorage.getItem('walletAddress')
})

const userTag = ref(store.getters.userTag || '')
const isEditing = computed(() => store.getters.userTag !== '')
const stats = computed(() => store.state.stats || {
  strength: 1,
  agility: 1,
  stamina: 1,
  luck: 1,
  intelligence: 1
})

const session = computed(() => store.state.session)
const bestScore = computed(() => store.getters.bestScore || 0)
const totalScore = computed(() => store.getters.totalScore || 0)
const multiplayerStats = computed(() => store.state.multiplayerStats || { wins: 0, loses: 0 })
const canUpgrade = computed(() => store.state.session?.isAuthenticated || false)

const showDisclaimer = ref(false)
const showAccountInfo = ref(false)
const tagError = ref('')
const isValidTag = ref(false)
const isCheckingTag = ref(false)
const password = ref('')
const passwordError = ref('')
const usePassword = ref(false)
const isValidPassword = ref(false)
const existingTagRequiresPassword = ref(false)
const currentPassword = ref('')
const newPassword = ref('')
const currentPasswordError = ref('')
const newPasswordError = ref('')
const isValidNewPassword = ref(false)

const isUnlinking = ref(false);
const isLinking = ref(false);

const walletAddress = ref(localStorage.getItem('walletAddress'))
const walletError = ref('');

// Проверяем, есть ли у пользователя пароль
const hasPassword = computed(() => {
  return session.value?.hasPassword || false
})

// Валидация user tag
const validateUserTag = () => {
  userTag.value = userTag.value.toLowerCase()
  const regex = /^[a-z0-9][a-z0-9_-]{0,10}[a-z0-9]$/
  
  if (!userTag.value) {
    tagError.value = ''
    isValidTag.value = false
    return
  }
  
  if (userTag.value.length < 2) {
    tagError.value = 'Tag must be at least 2 characters long'
    isValidTag.value = false
    return
  }
  
  if (userTag.value.length > 12) {
    tagError.value = 'Tag must not exceed 12 characters'
    isValidTag.value = false
    return
  }
  
  if (!regex.test(userTag.value)) {
    tagError.value = 'Tag can only contain a-z, 0-9, _, - and cannot start/end with _ or -'
    isValidTag.value = false
    return
  }
  
  tagError.value = ''
  isValidTag.value = true
}

const validatePassword = () => {
  if (!password.value) {
    passwordError.value = 'Password is required'
    isValidPassword.value = false
    return
  }
  
  if (password.value.length < 6) {
    passwordError.value = 'Password must be at least 6 characters long'
    isValidPassword.value = false
    return
  }
  
  passwordError.value = ''
  isValidPassword.value = true
}

const validateNewPassword = () => {
  if (!newPassword.value) {
    newPasswordError.value = 'Password is required'
    isValidNewPassword.value = false
  } else if (newPassword.value.length < 6) {
    newPasswordError.value = 'Password must be at least 6 characters'
    isValidNewPassword.value = false
  } else {
    newPasswordError.value = ''
    isValidNewPassword.value = true
  }
}

// Обработка user tag
const handleUserTag = async () => {
  if (!isValidTag.value || isCheckingTag.value) return
  
  isCheckingTag.value = true
  tagError.value = ''
  
  try {
    const response = await store.dispatch('checkUserTag', userTag.value)
    
    if (response.exists) {
      if (response.hasPassword) {
        tagError.value = 'User tag already has a password. Please use password to authenticate.'
        usePassword.value = true // Автоматически показываем поле для пароля
        isCheckingTag.value = false
        return
      }
    }
    
    // Если у пользователя нет пароля, продолжаем регистрацию/авторизацию
    await store.dispatch('authenticateUser', {
      userTag: userTag.value,
      password: usePassword.value ? password.value : null,
      walletAddress: publicKey.value?.toString() || null
    })
    // await store.dispatch('fetchUserGameHistory', userTag.value)
    // await store.dispatch('initSocket', { forceReconnect: true })
    
    password.value = ''
    passwordError.value = ''
    usePassword.value = false
  } catch (error) {
    console.log(error)
    tagError.value = error
  } finally {
    isCheckingTag.value = false
  }
}

// Обработка аутентификации через пароль
async function handlePasswordSubmit() {
  if (!password.value || !isValidPassword.value) {
    passwordError.value = 'Please enter a valid password'
    return
  }
  
  try {
    await store.dispatch('authenticateUser', { 
      userTag: userTag.value,
      password: password.value
    })
    await store.dispatch('fetchUserGameHistory', userTag.value)
    await store.dispatch('initSocket', { forceReconnect: true })
    
    // Очищаем все поля и сообщения после успешной авторизации
    password.value = ''
    passwordError.value = ''
    usePassword.value = false
    tagError.value = '' // Очищаем предупреждение о необходимости пароля
  } catch (error) {
    passwordError.value = error.message
  }
}

// // Прямая аутентификация (новый пользователь или без пароля)
// const handleDirectAuth = async () => {
//   try {
//     if (usePassword.value && isValidPassword.value) {
//       await store.dispatch('authenticateUser', { 
//         userTag: userTag.value,
//         password: password.value,
//         createWithPassword: true
//       })
//     } else {
//       await store.dispatch('authenticateUser', { 
//         userTag: userTag.value
//       })
//     }
//     await store.dispatch('fetchUserGameHistory', userTag.value)
//     password.value = ''
//   } catch (error) {
//     tagError.value = 'Authentication error. Please try again.'
//   }
// }

// // Аутентификация через кошелек
// async function handleWalletAuth() {
//   try {
//     if (!connected.value || !publicKey.value) {
//       throw new Error('Please connect your wallet first')
//     }

//     if (!isValidTag.value) {
//       throw new Error('Please enter a valid user tag')
//     }

//     await store.dispatch('authenticateWithWallet', { 
//       userTag: userTag.value
//     })
//     await store.dispatch('fetchUserGameHistory', userTag.value)
    
//     tagError.value = ''
//     isEditing.value = false
//   } catch (error) {
//     tagError.value = error.message
//   }
// }

// Выход из системы
const handleLogout = async () => {
  await store.dispatch('logout')
  userTag.value = ''
  password.value = ''
  usePassword.value = false
  tagError.value = ''
  passwordError.value = ''

  if(store.getters.socket) {
    await store.getters.socket.disconnect()
    await store.dispatch('initSocket', { forceReconnect: true })
  }
}

// Отключение кошелька
const unlinkWallet = () => {
  isUnlinking.value = true;
  store.dispatch('changeWallet', null)
  setTimeout(() => {
    store.dispatch('initializeAuth')
    isUnlinking.value = false;
  }, 1000)
}

const linkWallet = async () => {
  isLinking.value = true;
  try {
    walletError.value = '';
    await store.dispatch('changeWallet', publicKey.value.toString());
    await store.dispatch('initializeAuth');
  } catch (error) {
    walletError.value = error.message;
    setTimeout(() => {
      walletError.value = '';
    }, 10000); // Clear error after 5 seconds
  } finally {
    isLinking.value = false;
  }
}

// Описание текущего состояния аутентификации
const getAuthDescription = computed(() => {
  if (session.value?.isAuthenticated) {
    return 'You are now authenticated and can upgrade your character stats!'
  } else if (existingTagRequiresPassword.value) {
    return 'Please enter your password to continue.'
  } else if (usePassword.value) {
    return 'Create a password for your account (optional).'
  } else {
    return ''
  }
})

const handleWalletClick = (event) => {
  if (!store.state.disclaimerAgreed) {
    event.preventDefault()
    event.stopPropagation()
    showDisclaimer.value = true
  }
}

const handlePasswordClick = () => {
  if (!session.value?.isAuthenticated) {
    usePassword.value = !usePassword.value
  } else {
    usePassword.value = !usePassword.value
    currentPassword.value = ''
    newPassword.value = ''
    currentPasswordError.value = ''
    newPasswordError.value = ''
  }
}

const upgradeStat = (stat) => {
  if (!canUpgrade.value) {
    tagError.value = 'Please authenticate to upgrade your stats'
    return
  }
  
  if (stats.value[stat] < 10) {
    store.commit('setStat', { stat, value: stats.value[stat] + 1 })
  }
}

async function handleChangePassword() {
  try {
    await store.dispatch('changePassword', {
      currentPassword: hasPassword.value ? currentPassword.value : '',
      newPassword: newPassword.value
    })

    await store.dispatch('initializeAuth')
    
    currentPassword.value = ''
    newPassword.value = ''
    currentPasswordError.value = ''
    newPasswordError.value = ''
    usePassword.value = false
  } catch (error) {
    if (error.message.includes('current password')) {
      currentPasswordError.value = error.message
    } else {
      newPasswordError.value = error.message
    }
  }
}

const fetchHideSeekStats = async () => {
  if (session.value?.userTag) {
    await store.dispatch('fetchHideSeekPlayerStats', session.value.userTag)
  }
}

watch(() => session.value?.userTag, (newUserTag) => {
  if (newUserTag) {
    fetchHideSeekStats()
  }
})
</script>

